body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FLEX */
.flex {
  display: flex; }

.vertical {
  flex-direction: column; }
.horizontal, .horizontal-only {
  flex-direction: row; }
@media screen and (max-width: 720px){
  .horizontal {
    flex-direction: column; }
}

.flex-1 {
  flex: 1; }
.flex-2 {
  flex: 2; }
.flex-3 {
  flex: 3; }
.flex-4 {
  flex: 4; }
.flex-5 {
  flex: 5; }

.flex-center {
  justify-content: center;
  align-items: center; }

.wrap {
  flex-wrap: wrap; }

@media screen and (max-width: 720px){
  .overflow-x {
    overflow-x: scroll; }
}

.full-size {
  width: 100%;
}

.padding-vertical-10 {
  margin: 10px 0;
}

.wrapper, .container-small, .container-medium {
  margin: 0 auto;
  max-width: 1440px;
  width: calc(100%-10px);
  padding: 5px;
}
.container-small {
  max-width: 800px;
}
.container-medium {
  max-width: 1024px;
}

.text-center {
  text-align: center; }

.max-60 {
  flex-basis: 60px;
  max-width: 60px;
}

/* Cabeçalho */
.topbar {
  background-color: #272727;
  height: 5px;
  width: 100%;
  color: #FAFAFA;
}
.topbar i {
  color: #fafafa !important;
}
@media screen and (max-width: 720px){
  .topbar {
    height: auto;
    padding: 5px 0;
  }
}

.Header .pesquisa {
  width: 80%;
  padding: 10px 15px;
  border-radius: 10px;
  border: 0.5px solid #999;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-right: 0;
  font-size: 1rem;
}
@media screen and (max-width: 720px){
  .padding-10-mb {
    padding: 10px;
  }
}

.items-cabecalho {
  color: rgb(165, 134, 42);
  font-size: 0.8rem;
}
.items-cabecalho span {
  padding: 2px 3px;
  cursor: pointer;
}
.items-cabecalho span:hover {
  color: rgb(119, 94, 17);
}

.btn {
  padding: 10px 15px;
  border-radius: 10px;
  border: 0.5px solid #999;  
  cursor: pointer;
}
.btn-search {
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #474747;
  border:0;
  color: #FEFEFE;
}
.btn-search:hover {
  background-color: #222;
}

.telefones i {
  color: #222;
  padding: 0 5px;
}
.telefones span {
  font-size: 0.9rem;
}

.cart-container {
  color: #222;
}
.cart-container > span {
  background-color: #EBBD32;
  color: #222;
  padding: 2px 5px;
  border-radius: 50px;
  margin-top: -20px;
  margin-left: -10px;
  font-size: 0.9rem;
}

/* CATEGORIAS */
.Categorias {
  background-color: #272727;
  color: #F9F9F9;
  padding: 0 10px;
} 
.categoria-item {
  cursor: pointer;
  padding: 5px 10px;
  font-weight: bold;
}
.categoria-item:hover {
  color: #EBBD32;  
}

/* HERO SHOP */
.HeroShop {
  margin: 20px 10px;
}
.breadcrumbs {
  font-size: 0.8rem;
  color: #787878;
  margin: 10px;
}
.pics {
  padding: 20px; 
}
.pics-mini {
  padding: 20px 0;
}
.pic-mini {
  cursor: pointer;
}

.product-title {
  font-size: 2rem;
  margin-bottom: 0;
}
.cod {
  color: #767676;
  font-size: 0.9rem;
}
.descricao-short {
  padding: 30px 0;
  font-size: 1rem;
  line-height: 120%;
  color: #4a4a4a;
}
.tamanhos{
  padding: 15px 0 30px 0;
}
.tamanhos > div {
  background-color: #e1e1e1;
  padding: 15px;
  margin: 2px;
  cursor: pointer;
}
.tamanhos > div:hover {
  background-color: #cfcccc;
}

.preco {
  padding: 0 0 30px 0;
}
.preco-1 {
  color: #06650e;
  font-size: 1.8rem;
  font-weight: bold;
}
.preco-2 {
  color: #6a6a6a;
  font-size: 0.9rem;
}

.btn-cta {
  background-color: #ffc930;
  color: #000;
  font-weight: bold;
  border-radius: 0;
  border: 0;
  font-size: 1.2rem;
  padding: 15px 25px;
}
.btn-cta:hover {
  background-color: rgb(233, 185, 40);
}

.whishlist {
  padding: 10px 0;
  font-size: 0.9rem;
  color: rgb(113, 113, 113);
}
.whishlist i {
  padding:  0 5px 0 0;
}

.cep {
  font-size: 0.9rem;
  border-radius: 10px;
  border: 1px solid #777;
  padding: 10px 15px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.btn-frete {
  font-size: 0.9rem;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 0;
  background-color: #222;
  border: 0;
  color: #FAFAFA;
}

/* VIDEO */
.Video {
  height: 400px;
  background-color: #272727;
  padding: 15px 0;
}
.container-video {
  max-width: 600px;
  width: calc(100%-20px);
  padding: 10px;
  height: 95%;
  margin: 0 auto;
}

/* Descricao */
.Descricao {
  font-size: 1.2rem;
  line-height: 150%;
}
.descricao-header {
  background-color: #EBBD32;  
  cursor: pointer;
  color: #222;
}
.descricao-header > div:hover, .descricao-header > .active {
  background-color: rgb(194, 151, 21);  
}
.descricao-body {
  margin: 40px 0;
}
.Descricao td {
  padding: 10px;
}
.Descricao tr > td:first-child {
  background-color: #222;
  color: #F9F9F9;
}
.Descricao tr > td:last-child {
  border-bottom: 1px solid #222;  
}

/* ITEMS RELACIONADOS */
.foto-relacionado {
  height: 300px;
}
.card-relacionado {
  padding: 10px;
  cursor: pointer;
}
.titulo-relacionado {
  height: 40px;
}
.relacionado-data > span {
  color: #636363;
  font-size: 0.85rem;
}
.relacionado-data > span:first-child {
  font-size: 1.3rem;
  color: #06650e;
  font-weight: bold;
}

.btn-relacionado {
  color: #fff;
  background-color: #000000;
  border: 0;
  padding: 10px 25px;
  font-size: 1rem;
  margin: 10px 0; 
}

/* INSTITUCIONAL */
.Institucional {
  background-color: #171717;
  color: #FAFAFA;
  padding: 20px 10px;
  margin-top: 80px;
}
.Institucional a {
  color: #FAFAFA;
  text-decoration: none;
  line-height: 150%;
}
.Institucional a:hover {
  color: #EBBD32;
}
.newsletter {
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  padding: 8px 12px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.btn-newsletter {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border: 0;
  font-size: 1rem;
  color: #222;
  background-color: #EBBD32;
}
.btn-newsletter:hover {
  background-color: rgb(197, 154, 24);
}
.card-inside {
  padding: 2px;
}
/* FOOTer */
.Footer {
  color: #FEFEFE;
  background-color: #111;
  padding: 5px 10px;
  font-size: 0.8rem;
  text-align: center;
}

/* SIZES */
.only-mb {
  display: none;
}
@media screen and (max-width: 720px ){
  .no-mb {
    display: none;
  }
  .only-mb {
    display: flex;
  }
}